import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Item, Span, Image, Box } from 'react-html-email-browser';
import { connect } from 'react-redux';

import companies from 'src/data/companies.json';

const rowStyles = {
  padding: '0 0 0 16px',
  display: 'table',
  borderLeft: '3px solid #efefef',
};

const positionStyle = {
  fontFamily: 'Helvetica',
  padding: '0 0 10px 16px',
  fontWeight: 'bolder',
  color: '#000000',
  textTransform: 'uppercase',
};

const fullName = {
  position: 'relative',
  paddingBottom: '10px',
};

let imageStyle = {
  paddingBottom: '12px',
  boxSizing: 'content-box',
};

class CustomTemplate extends Component {
  static propTypes = {
    mainObj: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    sortedIds: PropTypes.arrayOf(PropTypes.number).isRequired,
  };

  getCompany = id => {
    const company = companies.find(elem => elem.id === id);
    if (company) return company;
    return {
      logo: '',
      name: '',
    };
  };

  render() {
    const { mainObj, sortedIds } = this.props;
    const itemRender = item => {
      const company = this.getCompany(item.value);
      if (sortedIds[0] === 15) {
        imageStyle = { ...imageStyle, paddingLeft: 0, borderLeft: 'none' };
      } else {
        imageStyle = {
          ...imageStyle,
          paddingLeft: '16px',
          borderLeft: '3px solid #efefef',
          paddingTop: '8px',
        };
      }
      switch (typeof item.value) {
        case 'string':
          if (item.id === 2) {
            return (
              <Item key={item.id} style={{ ...rowStyles, ...positionStyle }}>
                <Span fontSize={10}>{item.value}</Span>
              </Item>
            );
          } else if (item.id === 1) {
            return (
              <Item key={item.id} style={{ ...rowStyles, ...fullName }}>
                <Span fontSize={18} data-full="fullname">
                  {item.value}
                </Span>
              </Item>
            );
          }
          return (
            <Item key={item.id} style={{ ...rowStyles }}>
              <Span fontSize={12}>{item.value}</Span>
            </Item>
          );
        case 'number':
          return (
            <Item key={item.id} style={{ ...imageStyle }}>
              <Image
                width={company.logo.width}
                height={company.logo.height}
                src={company.logo.path}
                alt={company.value}
              />
            </Item>
          );
        default:
          return (
            <Item key={item.id}>
              <Span fontSize={12} style={{ ...rowStyles }}>
                {item.value.phoneType}
                {item.value.phone}
              </Span>
            </Item>
          );
      }
    };
    return (
      <Box align="left">
        {sortedIds.map(id => {
          const myItem = mainObj.find(item => item.id === id);
          if (myItem) return itemRender(myItem);
          return null;
        })}
        <Item align="left">
          <Span style={{ color: '#ffffff', fontSize: 10 }}>v2.0.1</Span>
        </Item>
      </Box>
    );
  }
}

const mSTP = state => ({
  phone: state.phone,
  mainObj: state.mainObjCustom,
  emails: state.emails,
  policy: state.policy,
  sortedIds: state.sortedIds,
});

export default connect(mSTP)(CustomTemplate);



