import React from 'react';
import {
  SortableContainer,
  SortableElement,
  SortableHandle,
} from 'react-sortable-hoc';
import Input from 'src/common/Input';
import Select from 'src/common/Select';
import companies from 'src/data/companies.json';
// import phoneTypes from 'src/data/phoneTypes.json';
import dots from 'src/assets/img/dots.svg';

// import styles from '../CustomForm.css';

const DragHandle = SortableHandle(() => (
  <span>
    <img src={dots} alt="dots" width="20" height="50" />
  </span>
));

const SortableItem = SortableElement(({ value }) => (
  <li style={{ listStyle: 'none', display: 'flex' }}>
    {value}
    <DragHandle />
  </li>
));

const SortableList = SortableContainer(
  ({ items, onChange, mainObj, handleChangePhone, handleDelete }) => (
    <ul style={{ padding: 0 }}>
      {items.map((item, i) => {
        const myItem = mainObj.find(obj => item.id === obj.id);

        if (item.id > 1000) {
          return (
            <SortableItem
              value={
                <Input
                  label={item.value}
                  id={item.id}
                  name={item.name}
                  type={item.type}
                  handleInput={e =>
                    onChange(e.target.name, e.target.value, item.id)
                  }
                  value={myItem && myItem.value}
                  button
                  decrement={() => handleDelete(item.id)}
                  reg={
                    /^[A-Za-zА-Яа-яЁё`'0-9_.+-]+@[A-Za-zА-Яа-яЁё`'0-9-]+\.[A-Za-zА-Яа-яЁё`'0-9-.]+$/
                  }
                />
              }
              index={i}
              key={item.id}
            />
          );
        } else if (item.id > 15) {
          return (
            <SortableItem
              value={
                <Input
                  id={item.id}
                  name={item.name}
                  label={item.value}
                  handleInput={e => {
                    handleChangePhone(e);
                  }}
                  type="phone"
                  value={myItem && myItem.value.phone}
                  button
                  decrement={() => handleDelete(item.id)}
                  reg={/^[a-z0-9 ,.'-\s]+$/i}
                />
              }
              index={i}
              key={item.id}
            />
          );
        } else if (item.id === 15) {
          return (
            <SortableItem
              value={
                <Select
                  onChange={e =>
                    onChange(e.target.name, +e.target.value, item.id)
                  }
                  label="Company"
                  values={companies}
                  name="customCompanyId"
                  id={item.id}
                  unselected
                  value={myItem && myItem.value}
                />
              }
              index={i}
              key={item.id}
            />
          );
        } else if (item.id < 15) {
          return (
            <SortableItem
              value={
                <Input
                  label={item.value}
                  id={item.id}
                  name={item.name}
                  type={item.type}
                  handleInput={e =>
                    onChange(e.target.name, e.target.value, +e.target.id)
                  }
                  value={myItem && myItem.value}
                  reg={/^[a-z0-9 ,.'-\s]+$/i}
                  decrement={() => handleDelete(item.id)}
                  button
                />
              }
              index={i}
              key={item.id}
            />
          );
        }
        return null;
      })}
    </ul>
  ),
);

export default SortableList;



