export const timeConverter = UNIXtimestamp => {
  let a = new Date();
  if (UNIXtimestamp) {
    a = new Date(UNIXtimestamp * 1000);
  }
  const months = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];
  const year = a.getFullYear();
  const month = months[a.getMonth()];
  const date = a.getDate();
  const hour = a.getHours();
  const min = a.getMinutes();
  const sec = a.getSeconds();

  const addZeros = time => {
    let timeWithZeros = time;
    if (time.toString().length < 2) {
      timeWithZeros = `0${time}`;
    }
    return timeWithZeros;
  };

  const time = `${date} ${month} ${year} ${addZeros(hour)}:${addZeros(
    min,
  )}:${addZeros(sec)}`;
  return time;
};

export const throttle = (func, limit) => {
  let inThrottle;
  return () => {
    if (!inThrottle) {
      func();
      inThrottle = true;
      // eslint-disable-next-line
      setTimeout(() => (inThrottle = false), limit);
    }
  };
};



