/* eslint-disable */
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Button from 'src/common/Button';
import SignTemplate from './SignTemplate/SignTemplate';
import CustomTemplate from './CustomTemplate/CustomTemplate';
import Clarification from './Сlarification/Clarification';
import MailPart from './MailPart/MailPart';
import { timeConverter, throttle } from '../../selectors/selectors';
import companies from '../../data/companies.json';

import styles from './Preview.css';

class Preview extends Component {
  static propTypes = {
    arrInputs: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    mainObj: PropTypes.shape().isRequired,
    isCustom: PropTypes.bool,
  };

  static defaultProps = {
    isCustom: false,
  };

  constructor() {
    super();
    this.state = {
      isSaved: false,
    };
    this.previewRef = React.createRef();
  }

  handleClick = data => {
    this.setState({ isSaved: true });
    setTimeout(() => {
      this.setState({ isSaved: false });
    }, 2000);
    const target = this.previewRef.current;
    let rng;
    let sel;
    if (document.createRange) {
      rng = document.createRange();
      rng.selectNode(target);
      sel = window.getSelection();
      sel.removeAllRanges();
      sel.addRange(rng);
      document.execCommand('copy');
    } else {
      rng = document.body.createTextRange();
      rng.moveToElementText(target);
      rng.select();
      document.execCommand('copy');
    }

    const getItemLocal = localStorage.getItem('savedData');

    const isUnique =
      getItemLocal &&
      JSON.parse(getItemLocal).some(item => {
        delete item.time;
        delete item.id;
        const a = {
          ...data,
          time: timeConverter(),
          value: companies[data.companyId - 1].value,
          id: (getItemLocal || []).length,
        };
        delete a.time;
        delete a.id;
        return JSON.stringify(item) === JSON.stringify(a);
      });

    if (isUnique) return;

    const nextData = [
      ...(getItemLocal ? JSON.parse(getItemLocal) : {}),
      {
        ...data,
        time: timeConverter(),
        value: companies[data.companyId - 1].value,
        id: (getItemLocal || []).length,
      },
    ];

    if (nextData.length > 10) {
      nextData.shift();
    }

    localStorage.setItem('savedData', JSON.stringify(nextData));
  };

  render() {
    const { mainObj, isCustom, arrInputs } = this.props;

    const { isSaved } = this.state;

    return mainObj.companyId || arrInputs.length ? (
      <Fragment>
        <div className={styles.containerInner}>
          <MailPart />
          <div className={styles.templateWrapper} ref={this.previewRef}>
            {isCustom ? <CustomTemplate /> : <SignTemplate />}
          </div>
        </div>
        <div className={styles.buttonWrapper}>
          <Button
            className={styles.button}
            handleClick={throttle(() => this.handleClick(mainObj), 3000)}
            title="Copy Signature"
          />
          <span className={isSaved ? styles.isSavedPopup : styles.popup}>
            Signature copied
          </span>
        </div>
      </Fragment>
    ) : (
      <Clarification isCustom={isCustom} />
    );
  }
}

const mSTP = state => ({
  mainObj: state.mainObj,
  arrInputs: state.inputs,
});
export default connect(mSTP)(Preview);



